<template>
  <div
    class="row"
  >
      <div
          class="col-12 question-card"
      >
        <div
          class="card mt-2"
          v-bind:class="{
            'is-training-card': is_qualification_training,
            'is-test-card': is_qualification_test,
            'is-error-card': is_qualification_error,
          }"
        >
          <a class="card-header"
             style="color: mediumblue; background-color: aliceblue; text-align: left;" v-if="is_qualification_training">
              <span style="font-size: 24px"><strong> Training {{index}} </strong></span>
          </a>
          <a class="card-header"
             style="color: darkgreen; background-color: lightgoldenrodyellow; text-align: left;" v-if="is_qualification_error">
              <span style="font-size: 24px"><strong> Review the Error {{index}} </strong></span>
          </a>
          <a class="card-header"
             style="color: darkgreen; background-color: lightgoldenrodyellow; text-align: left;" v-if="is_qualification_test">
              <span  style="font-size: 24px"><strong> Test {{index}} </strong></span>
          </a>
          <div class="card-body">
            <h5 class="card-title annotation-passage" style="text-align: left">
              <span>
                <strong>Question : </strong>{{annotation.goal}}<br/><br/>
                <strong>candidate1 : </strong>{{annotation.can1}}<br/>
                <strong>candidate2 : </strong>{{annotation.can2}}
              </span></h5>
            <div class="card-text" style="text-align: left">
              <v-divider></v-divider><br/>
              <div>
                <p>
                   <strong style="font-size: 18px"> 1. Which option (candidate1, candidate2) is the most appropriate answer to the Question? </strong>
                </p>
                <p
                        class="form-check form-check-inline d-flex"
                        v-for="(resp, idx) in annotation.candidates1"
                        v-bind:key="idx"
                        style="margin-left: 25px"
                >
                  <input class="form-check-input"
                         :id="'q1-' + resp + index"
                         type="radio"
                         v-bind:name="'q1-' + index"
                         v-bind:value="resp"
                         v-model="annotation.picked1"
                         v-if="annotation.Q1_ans !== ''"
                         v-bind:class="{
                          'is-valid': is_correct_answer_q1 && has_correct_answer_q1 && has_picked_answer1,
                          'is-invalid': is_wrong_answer_q1 && has_correct_answer_q1 && has_picked_answer1,
                       }"
                  >

                  <input class="form-check-input"
                         :id="'q1-' + resp + index"
                         type="radio"
                         v-bind:name="'q1-' + index"
                         v-bind:value="resp"
                         v-model="annotation.picked1"
                         v-if="annotation.Q1_ans === ''"
                  >
                  <label class="form-check-label" :for="'q1-' + resp + index">{{render_radio_q1(resp)}}</label>
                </p>
                <div v-if="annotation.Q1_explanation !== ''">
                  <div class="alert alert-danger" role="alert" v-if="is_wrong_answer_q1 && has_correct_answer_q1 && has_picked_answer1">
                    Incorrect. {{annotation.Q1_explanation}}
                  </div>
                  <div class="alert alert-success" role="alert" v-else-if="!is_wrong_answer_q1 && has_correct_answer_q1 && has_picked_answer1">
                    Correct. Good job!
                  </div>
                </div>
              </div><br/><br/>

              <div>
                <p>
                  <strong style="font-size: 18px"> 2. Which of the following questions best suits the Incorrect? </strong>
                </p>
                <p
                        class="form-check form-check-inline d-flex"
                        v-for="(resp, idx) in annotation.candidates2"
                        v-bind:key="idx"
                        style="margin-left: 25px"
                >
                  <input class="form-check-input"
                         :id="'q2-' + resp + index"
                         type="radio"
                         v-bind:name="'q2-' + index"
                         v-bind:value="resp"
                         v-model="annotation.picked2"
                         v-if="annotation.Q2_ans !== ''"
                         v-bind:class="{
                          'is-valid': is_correct_answer_q2 && has_correct_answer_q2 && has_picked_answer2,
                          'is-invalid': is_wrong_answer_q2 && has_correct_answer_q2 && has_picked_answer2
                       }"
                  >
                  <input class="form-check-input"
                         :id="'q2-' + resp + index"
                         type="radio"
                         v-bind:name="'q2-' + index"
                         v-bind:value="resp"
                         v-model="annotation.picked2"
                         v-if="annotation.Q2_ans === ''"
                  >
                  <label class="form-check-label" :for="'q2-' + resp + index">{{render_radio_q2(resp)}}</label>
                </p>
                <div v-if="annotation.Q2_explanation !== ''">
                  <div class="alert alert-danger" role="alert" v-if="is_wrong_answer_q2 && has_picked_answer2 && has_correct_answer_q2">
                    Incorrect. {{annotation.Q2_explanation}}
                  </div>
                  <div class="alert alert-success" role="alert" v-else-if="!is_wrong_answer_q2 && has_picked_answer2 && has_correct_answer_q2">
                    Correct. Good job!
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <br/>
      </div>
  </div>
</template>


<script>
export default {
  name: 'Question',
  props: {
    annotation: {
      type: Object,
      required: true,
    },
    index: {
      type: String,
      required: true,
    },
  },

  computed: {
    is_qualification_training: function () {
      return this.annotation.isTest !== 'y' && this.annotation.Q1_ans !== '' && this.annotation.Q2_ans !== '';
    },
    is_qualification_test: function () {
      return this.annotation.Q1_ans === '' && this.annotation.Q2_ans === '';
    },
    is_qualification_error: function () {
      return this.annotation.isTest === 'y' && this.annotation.Q1_ans !== '' && this.annotation.Q2_ans !== '';
    },


    has_picked_answer1: function () {
      return this.annotation.picked1 !== null;
    },
    has_picked_answer2: function () {
      return this.annotation.picked2 !== null;
    },


    is_wrong_answer_q1: function () {
      return this.annotation.picked1 !== this.annotation.Q1_ans;
    },
    is_wrong_answer_q2: function () {
      return this.annotation.picked2 !== this.annotation.Q2_ans;
    },


    is_correct_answer_q1: function () {
      return this.annotation.picked1 === this.annotation.Q1_ans;
    },
    is_correct_answer_q2: function () {
      return this.annotation.picked2 === this.annotation.Q2_ans;
    },


    has_correct_answer_q1: function () {
      return this.annotation.Q1_ans !== null && this.annotation.Q1_ans !== "";
    },
    has_correct_answer_q2: function () {
      return this.annotation.Q2_ans !== null && this.annotation.Q2_ans !== "";
    },
  },

  watch: {
    annotation() {
      this.$emit('input', this.annotation);
    }
  },

  methods: {
    render_radio_q1(value){
      if (value === '1'){
        return "candidate1";
      }
      if (value === '2'){
        return "candidate2";
      }
    },

    render_radio_q2(value){
      if (value === '1'){
        return this.annotation.noise1;
      }
      if (value === '2'){
        return this.annotation.noise2;
      }
      if (value === '3'){
        return this.annotation.noise3;
      }
      if (value === '4'){
        return "None of the above";
      }
    },

  },

}
</script>



<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>

.card.is-training-card{
  border-color: deepskyblue;
  border-style: solid;
  border-width: 2px;
}

.card.is-test-card{
  border-color: yellowgreen;
  border-style: solid;
  border-width: 2px;
}

.card.is-error-card{
  border-color: darkgreen;
  border-style: solid;
  border-width: 2px;
}

</style>
