              But you have <span style="color: blue">{{this.remainingAttempts}} more chances</span> to attempt the check your answer.<br>
              You can either modify your answers above and re-evaluate it. <br/>
              If you want to stop here, you can submit this hit using the wide blue &lt;Click here to submit HIT&gt; button below.
            </strong>
        </div>

<!--        제출기회 3번 다 하고 결국 통과못했을 때-->
        <div v-else-if="tried && blacklisted && attempts===3 && !submit"
             class="alert alert-danger"
             role="alert"
             style="font-size: 18px">
