              Please submit your response by using the wide blue &lt;Click here to submit HIT&gt; button below. Thank you :)</strong>
        </div>

<!--        blacklisted user가 다시 접근 했을 때 or 중도 포기 후 제출 or 3번 실패하고 제출 후-->
        <div v-else-if="blacklisted&&!tried || blacklisted&&submit"
             class="alert alert-danger"
             role="alert"
             style="font-size: 18px">
            <strong>Sorry. You finally failed to pass the test. Thank you :)</strong>
