            If everything looks good, you can submit this hit using the wide blue &lt;Click here to submit HIT&gt; button below.<br/>
          </strong>
        </div>


        <div v-if="qualified && (this.errors_qid.length !== 0) && !submit">
          <br/><br/><br/>
          <span style="text-align: center; font-size: 40px; color: darkgreen"> Review the wrong questions </span>
          <question
                  v-for="(item, key) in error_samples"
                  v-bind:key="key"
                  v-bind:index="key.toString()"
                  v-bind:annotation="item"
          >
          </question>
        </div>

        <div v-if="training_passed && can_continue && !submit && !error_occurred">
          <button
                type="button"
                class="btn btn-primary btn-lg btn-block"
                v-if="training_passed && can_continue"
                v-on:click="submitTestResultToUs(example_test)"
                :disabled="!this.all_answered || (blacklisted && tried) || qualified || submit"
                aria-label="Make sure all exam questions are checked."
                data-balloon-pos="up"
                style="color: white; background-color: darkgreen;"
          ><strong>Check the answers (please wait a second)</strong></button>
        </div>
        <br>

        <div v-if="tried && !submit">
          <button
                type="button"
                class="btn btn-primary btn-lg btn-block"
                v-on:click="submitHit()"
                :disabled="submit || (qualified&&!all_reviewed)"
                style="color: white"
          ><strong>Click here to submit HIT</strong></button>
        </div>
        <br>


<!--        <div v-if="(!submit&&!tried&&qualified) || (submit&&qualified)">-->
<!--          <button-->
<!--                type="button"-->
<!--                class="btn btn-primary btn-lg"-->
<!--                v-on:click="move_to_real_work"-->
<!--                style="color: white; padding:15px; background-color: slateblue;"-->
<!--          ><strong>Move on to Data Creation workspace</strong></button>-->
<!--        </div>-->
        <br>


        <div>
          <form
                  method='post'
                  name='mturk_form'
                  id='mturk-external-submit-form'
                  v-bind:action="mturk_params.turkSubmitTo"
                  type='hidden'
          >
            <input type='hidden' name='assignmentId' id='assignmentId' v-bind:value="mturk_params.assignmentId"/>
            <input type='hidden' name='pay' id='pay' v-bind:value="pay"/>
            <input type='hidden' name='qualified' id='qualified' v-bind:value="qualified"/>
            <input type="hidden" name="generated_answers" v-model="generated_answers.answers">
<!--            <input type='submit' class="btn btn-success btn-sm" id='submitButton' name="submitButton" value='Click here to submit HIT'>-->
          </form>

          <div class="alert alert-danger" role="alert" v-if="show_submission_alert">
            There's an error in submission.
          </div>
        </div>
      </div>
      <br/><br/>
    </div>
  </div>
