<script>
document.title = "Qualification"

import Vue from 'vue';
import 'bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import queryString from "query-string";
import _ from 'lodash';
import Papa from "papaparse";
import axios from "axios";
import 'balloon-css';
import { v4 as uuidv4} from 'uuid';

import Training_Explanation from "./components/Training_Explanation.vue";
import Question from './components/Question.vue';
import Introduction from './components/Introduction.vue'


export default {
  name: 'app',
  components: {
    'Introduction': Introduction,
    'Question': Question,
    'Training_Explanation': Training_Explanation,
  },

  data: function(){
    return {
        active_tab: "introduction",

        example_training: {},
        example_test: {},
        gold_samples: {},
        error_samples: {},

        mturk_params: {
          assignmentId: "",
          hitId: "",
          turkSubmitTo: "",
          workerId: "",
          isPreview: false,
        },

        generated_answers: {    // JSON file 로 저장한 답안
          answers: "",
        },

        show_submission_alert: false,

        number_of_test_questions: 8,
        score: 0,

        tried: false,
        blacklisted: false,         // qualification test 시도 가능 횟수 초과되서 더 이상 도전 불가!
        qualified: false,           // qualification test 통과 --> real creation task 페이지로 이동! (URL 제공)
        submit: false,
        remainingAttempts: 3,
        attempts: 0,
        verify_response: {},
        basepay: 0.01,
        passpay: 1.0,
        pay: 0.0,
        errors_qid: [],

        backend_endpoint: "",
        host_name: "",
        loading: false,
        error_occurred: false
    }
  },

  filters: {          // apply common text formatting. be usable in two places: mustache interpolations and v-bind.
    currency: function(value){
      return value.toFixed(2);      // 소수점 아래 2자리까지만 표시.
    }
  },

  computed: {
    training_passed: function(){        // training section 에 있는 questions 에 모두 답이 체크가 되어 있는지
      return _.every(this.example_training, q => {return q.picked1 !== null && q.picked2 !== null})
    },
    all_answered(){
      return _.every(this.example_test, q => {return q.picked1 !== null && q.picked2 !== null})
    },
    all_reviewed(){
      return _.every(this.error_samples, q => {return q.picked1 !== null && q.picked2 != null})
    },
    show_tab_introduction: function(){
      return this.active_tab === "introduction"
    },
    show_tab_overview: function(){
      return this.active_tab === "overview"
    },
    show_tab_qualification_learning: function(){
      return this.active_tab === "qualification-training"
    },
    show_tab_qualification_test: function(){
      return this.active_tab === "qualification-test"
    },

    local_debug_mode: function(){
      return this.backend_endpoint === "127.0.0.1:9050" || this.host_name === 'localhost'
    },
    protocol: function(){
      return this.local_debug_mode? "https://" : "https://";
    },
    has_response: function(){
      return Object.keys(this.verify_response).length > 0;
    },
    can_continue: function(){       // 아직 test 볼 수 있으면! 즉 test 통과는 못했지만 시도 기회는 남아 있는 상태.
      return !this.qualified && !this.blacklisted;
    },
  },

  async mounted() {
      const parsed_query_string = queryString.parse(window.location.search);    // return query part of url as dictionary
      console.log(parsed_query_string)       // Mturk --> {assignmentId: , hitId: , turkSubmitTo: , workerId: }
      const url = window.location
      this.host_name = url.hostname
      // console.log(url)
      if (this.local_debug_mode){
        console.log("This is the local test mode.")
      }
      let assignmentId = parsed_query_string.assignmentId;
      if(assignmentId === null || assignmentId === undefined){
        assignmentId = "TestAssignmentId";
      }
      if (assignmentId === "ASSIGNMENT_ID_NOT_AVAILABLE"){
        console.log("Preview mode.");
        this.mturk_params.isPreview = true;
      }
      this.backend_endpoint = 'backend_endpoint' in parsed_query_string ?  parsed_query_string.backend_endpoint: "127.0.0.1:9050";
      this.basepay = 'basepay' in parsed_query_string ?  parseFloat(parsed_query_string.basepay): 0.01;
      this.passpay = 'passpay' in parsed_query_string ?  parseFloat(parsed_query_string.passpay): 1.0;
      this.remainingAttempts = 'remainingAttempts' in parsed_query_string ?  parseFloat(parsed_query_string.remainingAttempts): 3;
      this.attempts = 'attempts' in parsed_query_string ?  parseFloat(parsed_query_string.attempts): 0;
      this.active_tab = 'tab' in parsed_query_string ? parsed_query_string.tab : "introduction";
      if(this.active_tab!=="introduction" &&this.active_tab!=="overview" &&this.active_tab!=="qualification-training" &&this.active_tab!=="qualification-test"){
        this.active_tab = "introduction";
      }

      const hitId = parsed_query_string.hitId;
      const turkSubmitTo = parsed_query_string.turkSubmitTo;
      let workerId = parsed_query_string.workerId;
      if(workerId === null || workerId === undefined){
        workerId = "TestUser";
      }

      this.mturk_params.turkSubmitTo = turkSubmitTo + "/mturk/externalSubmit";
      this.mturk_params.workerId = workerId;
      this.mturk_params.hitId = hitId;
      this.mturk_params.assignmentId = assignmentId;
      console.log("mturk_params: ", this.mturk_params)

      const self = this;

      const worker_verify_url = this.protocol + this.backend_endpoint + "/can_work";    // I'd like to kow worker's state.  qualified? blacklisted?
      // const worker_verify_url = this.protocol + this.backend_endpoint;
      console.log("checking eligibility at: " + worker_verify_url);
      axios.get(worker_verify_url, {        // return  https:// ~~~~~~ /can_work?worker=workerId
        params: {
          worker: workerId
        }
      })
      .then(function (response) {       // response 는 dictionary 형식의 json 으로 함수 내에서 정의한 attempts, remainingAttempts, qualified, blacklisted 를 반환
        self.blacklisted = response.data.blacklisted;
        self.qualified = response.data.qualified;
        self.attempts = response.data.attempts;
        if(self.qualified){
          self.pay = self.passpay;
        }
        else{
          self.pay = self.basepay;
        }
        console.log("Check user can_work: ", response.data)
      })
      .catch(function (error) {
        console.log(error);
        self.error_occurred = true;
      });

      try{
        Papa.parse(" ./qualification_training.csv", {
          download: true,
          header: true,
          complete: function(results) {
            const data = results.data;
            _.each(data, (example, idx) => {
              example.question_id = "qualification-training-" + idx;
              example.candidates1 = ['1', '2'];
              example.candidates2 = ['1', '2', '3', '4'];
              example.picked1 = null;
              example.picked2 = null;
              self.$set(self.example_training, idx+1 ,example);
            })
            console.log("Complete to load", results.data.length, "Training Data.");
          }
        })
        // Reading testing data.
        this.load_test_csvs();
        this.load_gold_csvs();
      }
      catch (e) {
        console.log(e);
      }
  },

  methods: {
    switch_tab: function(tab){
      this.active_tab = tab;
    },

    load_test_csvs: function(){
        const self = this;
        self.example_test = {};

        Papa.parse("./qualification_test.csv", {
          download: true,
          header: true,
          error: function () {
            self.error_occurred = true;
          },
          complete: function (results) {
            let data = _.map(results.data, (example) => {      // map is role as 'for'
              if (example.question_id === null || example.question_id === undefined) {
                return null;
              }
              if (example.workerId === '' || example.workerId === null || example.workerId === undefined || example.workerId === '') {
              example.workerId = "TestUser";
              }
              example.candidates1 = ['1', '2'];
              example.candidates2 = ['1', '2', '3', '4'];
              example.picked1 = null;
              example.picked2 = null;
              return example;
            });
            data = _.filter(data, (x) => {
              return x !== null
            });
            // data = _.sampleSize(data, self.number_of_test_questions);
            console.log("Complete to load", data.length,"Test Data.");
            _.each(data, (example, idx) => {
              self.$set(self.example_test, idx+1, example)
            })
          }
        });
      },

    load_gold_csvs: function(){
      const self = this;
      self.gold_samples = {};

      Papa.parse("./test-gold-label.csv", {
        download: true,
        header: true,
        error: function () {
          self.error_occurred = true;
        },
        complete: function (results) {
          let data = _.map(results.data, (example) => {      // map is role as 'for'
            if (example.question_id === null || example.question_id === undefined) {
              return null;
            }
            if (example.workerId === '' || example.workerId === null || example.workerId === undefined || example.workerId === '') {
            example.workerId = "TestUser";
            }
            example.candidates1 = ['1', '2'];
            example.candidates2 = ['1', '2', '3', '4'];
            example.picked1 = null;
            example.picked2 = null;
            return example;
          });
          data = _.filter(data, (x) => {
            return x !== null
          });
          // data = _.sampleSize(data, self.number_of_test_questions);
          console.log("Complete to load", data.length, "Gold Labels");
          _.each(data, (example, idx) => {
            self.$set(self.gold_samples, idx+1, example)
          })
        }
      });
    },

    submitTestResultToUs: function(Objects){
      this.tried = true
      this.generated_answers.answers = JSON.stringify(Objects);       // json file 로 변수에 저장.
      const post_data = {
        "answers": this.generated_answers.answers,        // json string type
        "workerId": this.mturk_params.workerId,
        "assignmentId": this.mturk_params.assignmentId,
        "turkSubmitTo": this.mturk_params.turkSubmitTo
      };

      const post_config = {
        headers: {'Access-Control-Allow-Origin': '*'}
      };
      const verify_endpoint = this.protocol + this.backend_endpoint + "/verify";    //the url to submit the answers, when the worker hit on the submit buttons.
      // const verify_endpoint = this.protocol + this.backend_endpoint;
      this.loading = true;
      const self = this;
      axios.post(verify_endpoint, post_data, post_config).then(data => {
        console.log(data.data);
        self.verify_response = data.data;
        self.qualified = self.verify_response['qualified'] === "1";
        self.blacklisted = self.verify_response['blacklisted'] === "1";
        self.remainingAttempts = parseInt(self.verify_response['remainingAttempts']);
        self.attempts = parseInt(self.verify_response['attempts']);
        self.score = parseInt(self.verify_response['acc'])
        self.errors_qid = self.verify_response['errors_qid'];

        if (this.qualified){
          self.pay = self.passpay
          this.make_error_samples(self.errors_qid)
          scroll(0,0)
        }
        else{
          self.pay = self.basepay
        }
      })
      .catch(error => {
        self.error_occurred = true;
        console.log(error);
      });

      this.loading = false;
      window.scrollTo(0,document.body.scrollHeight)
    },

    submitHit: function(){
      this.submit = true
      if (this.attempts>0 && !this.qualified){
        this.blacklisted = true
        const worker_give_up_url = this.protocol + this.backend_endpoint + "/give_up";

        axios.get(worker_give_up_url, {        // return  https:// ~~~~~~ /can_work?worker=workerId
          params: {
            worker: this.mturk_params.workerId,
            turkSubmitTo: this.mturk_params.turkSubmitTo,
          }
        })
        .then(function (response) {       // response 는 dictionary 형식의 json 으로 함수 내에서 정의한 attempts, remainingAttempts, qualified, blacklisted 를 반환
          self.blacklisted = response.data.blacklisted;
          self.pay = self.basepay;
          // console.log(response.data);
          console.log("User Give up")
        })
        .catch(function (error) {
          console.log(error);
          self.error_occurred = true;
        });
      }

      if(this.local_debug_mode){
        console.log("Local testing mode. Not submitting to mturk.");
      }
      else{
        Vue.nextTick(function () {
        document.getElementById('mturk-external-submit-form').submit();
      });
      }
    },

    make_error_samples(errors_qid){
        this.error_samples = {};
        Object.values(this.gold_samples).map((answer, idx)=>{
          if (errors_qid.includes(answer.question_id)){
            answer.question_id = "error-id-" + idx;
            answer.candidates1 = ['1', '2'];
            answer.candidates2= ['1', '2', '3', '4'];
            answer.picked1 = null;
            answer.picked2 = null;
            this.$set(this.error_samples, idx + 1, answer);
          }
        })
        console.log("Complete to generate", this.error_samples.length, "review samples");
    },
    // move_to_real_work (){
    //   window.open("https://workersandbox.mturk.com/projects/3PT8QOKVB0GHSUSI7TQKK84GEK354I/tasks?ref=w_pl_prvw");
    // },
  },
}
</script>


